<template>
  <el-row v-for="project in projectList" :key="project.id">
    <el-col :span="4">
      项目名称：{{project.projectName}}
    </el-col>
    <el-col :span="4">
      提出人：{{project.proposer}}
    </el-col>
    <el-col :span="4">
      验收人：{{project.checker}}
    </el-col>
    <el-col :span="4">
      PM：{{project.pm}}
    </el-col>
    <el-col :span="4">
      状态：{{project.status}}
    </el-col>
    <el-col :span="4">
      <a href="#" >创建项目</a>
      <el-button type="primary">创建项目</el-button>
    </el-col>
  </el-row>

  <el-button text @click="dialogFormVisible = true">open a Form nested Dialog</el-button>

  <el-dialog v-model="dialogFormVisible" title="创建项目">
    <el-form :model="projectForm">
      <el-form-item label="项目名称" :label-width="formLabelWidth">
        <el-input v-model="projectForm.projectName" autocomplete="off" />
      </el-form-item>
      <el-form-item label="提出人" :label-width="formLabelWidth">
        <el-input v-model="projectForm.proposer" autocomplete="off" />
      </el-form-item>
      <el-form-item label="验收人" :label-width="formLabelWidth">
        <el-input v-model="projectForm.checker" autocomplete="off" />
      </el-form-item>
      <el-form-item label="项目经理" :label-width="formLabelWidth">
        <el-input v-model="projectForm.pm" autocomplete="off" />
      </el-form-item>
      <el-form-item label="状态" :label-width="formLabelWidth">
        <el-input v-model="projectForm.status" autocomplete="off" />
      </el-form-item>

<!--      <el-form-item label="Zones" :label-width="formLabelWidth">
        <el-select v-model="form.region" placeholder="Please select a zone">
          <el-option label="Zone No.1" value="shanghai" />
          <el-option label="Zone No.2" value="beijing" />
        </el-select>
      </el-form-item>-->
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取消</el-button>
        <el-button type="primary" @click="createProject"
        >确认</el-button
        >
      </span>
    </template>
  </el-dialog>

</template>

<script setup>
/* eslint-disable */
import axios from 'axios'
import {onMounted, reactive, ref} from 'vue'
import { ElMessage } from 'element-plus'

const dialogFormVisible = ref(false)
const formLabelWidth = '140px'
const form = reactive({
  name: '',
  region: '',
  date1: '',
  date2: '',
  delivery: false,
  type: [],
  resource: '',
  desc: '',
})

const projectList = reactive([])
const projectForm = reactive({
  projectName: '',
  proposer: '',
  checker: '',
  pm: '',
  status: ''
})

const fetchProjectList = async function (){
  const result = await axios.get('/bpi2/project/list',{});
  for(let index in result.data){
    projectList.push(result.data[index])
    console.log(result.data[index])
  }
  // projectList = result.data;
}
const createProject = async function(){
  const res = await axios.post('/project/create',projectForm);
  if(res.status == 200){
    ElMessage({
      message: '创建成功',
      type: 'success',
    })
    dialogFormVisible.value = false
  }
  console.log(projectForm)

}

const initFormData = function(){
  this.projectForm.projectName = '';
  this.projectForm.proposer = '';
  this.projectForm.checker = '';
  this.projectForm.pm = '';
  this.projectForm.status = '';
}
onMounted(
    fetchProjectList
)
</script>

